<template>
  <div id="song">
<!--       <div class="search1" style="height:52px;">     
      <router-link to="/" ><mt-button style="height: 8vh;float:left;"><i class="fa fa-long-arrow-left" aria-hidden="true"></i>
      </mt-button></router-link>  
      <mt-search
          v-model="value"
          cancel-text="取消"
          placeholder="搜索" style="float:left;width:88%;">
        </mt-search>             
      </div>  -->
        <router-link to="/" ><mt-button style="height: 8vh;float:left;"><i class="fa fa-long-arrow-left" aria-hidden="true"></i>
        </mt-button></router-link>      
        <mt-search autofocus v-model="value" :result="filterResult"></mt-search>
  
  </div>
</template>

<script>
import { Search } from 'mint-ui';
export default {
  name: 'song',
  data () {
    return {
        value: '',
         defaultResult: [
           'Alin',
           'Bigbang',
           'chenxiaochun',
           'Dasheng',
           'ellen',
           'feilunhai',
           'Cherry',
           'Berry',
           'Core',
           'Fig',
           'Haw',
           'Melon',
           'Plum',
           'Pear',
           'Peanut',
           'Other',
           'slilena'
         ]
    }
  },
  computed: {
     filterResult() {
       return this.defaultResult.filter(value => new RegExp(this.value, 'i').test(value));
     }
   }
}
</script>

<style>
*{
  padding: 0;
  margin:0;
}
#song{
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  color: #2c3e50;
  margin-top: -60px;
  text-align: left;
}
.mint-search-list-warp{
  margin-top: 10px;
}
</style>



